import { EventEmitter } from '@angular/core';
export class Menu {
    nombre:string;
    icono:string;
    path:string;

}

export class Album {
    id:number;
    path:string;
    widthImg:number;
    heigthImg:number;

}

export class Opinion {
    opinion:string;
    persona:string;

}

export class Disparador {
    static  OnChangeComponent = new EventEmitter();

}

export class Servicios {
    readonly id:string;
    readonly titulo:string;
    readonly descripcion:string;
    readonly servicios:string[];
    readonly imagen:string;
}

export class Scroll{
    static move(top=0, left=0)
    {
        window.scrollTo({
            top:top,
            left:left,
            behavior: 'smooth'
        });  
    }
}