
import { Directive, ElementRef, OnInit } from '@angular/core';
import { NavigationStart, Router } from '@angular/router';

@Directive({
  selector: '[appShow]'
})
export class ShowDirective implements OnInit{

  constructor(
    public element: ElementRef,
    private ruta: Router
  ) {

    
   }

   ngOnInit()
   {
    
    this.AgregarEvento();
    let sons = this.element.nativeElement.childNodes;    
    
    this.OnViewPort(sons)             
   }

   AgregarEvento()
   {
    let sons = this.element.nativeElement.childNodes;    
    this.ChangeAspect(sons);
    window.addEventListener("load", ()=>{
      this.OnViewPort(sons)             
    })
    
    window.addEventListener("scroll", ()=>{
      this.OnViewPort(sons)             
     
    }) 

    window.addEventListener("resize", ()=>{
      this.OnViewPort(sons)             
    })

  

   }

   ChangeAspect(sons)
   {
    for(let recorre of sons)
    {
      recorre.style.visibility = 'hidden';
      recorre.style.opacity = '0';
      recorre.style.transition = '1.8s';
      recorre.style.transitionDelay = '0.2s';
      
      recorre.style.transform = 'translateY(35px)'
    }
   }
   

   OnViewPort(sons)
   {
    let altoViewPort = window.innerHeight;
    let distanciaElementViewPort = this.element.nativeElement.getBoundingClientRect().top
    let diferencia = Number(distanciaElementViewPort.toFixed(0)) - Number(altoViewPort.toFixed(0))
    if(diferencia < -70)
    {
      
      for(let recorre of sons)
      {
        recorre.style.transform = 'translateY(0px)'
        recorre.style.visibility = 'visible';
        
        recorre.style.opacity = '1';
      }
      
    }
   }
   

}

