import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Scroll } from 'src/app/interface/menu';
import {Validate} from 'is-valid-value'

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  constructor(
    private ruta: Router
  ) { }

  ngOnInit() {
    
  }
  GoTo(modulo,idParam?)
  { 
    if(Validate.value(idParam))
    {
      this.ruta.navigate([modulo, {id:idParam}])    
    }else{
      this.ruta.navigate([modulo])
    }
    Scroll.move();
  }

  ScrollTo(id)  
  {
    const element = document.getElementById("elegirnos").getBoundingClientRect;
    
    window.scrollTo({
      top:0,
      left:0,
      behavior: 'smooth'
    });
  }

}
