import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [  
  {  
    path:'',
    loadChildren: ()=>import('./inicio/inicio.module').then(x=> x.InicioModule)
  },
  {  
    path:'inicio',
    loadChildren: ()=>import('./inicio/inicio.module').then(x=> x.InicioModule)
  },
  {  
    path:'servicios',
    loadChildren: ()=>import('./servicios/servicios.module').then(x=> x.ServiciosModule)
  },
  {  
    path:'contactanos',
    loadChildren: ()=>import('./contactanos/contactanos.module').then(x=> x.ContactanosModule)
  },
  {  
    path:'trabaja',
    loadChildren: ()=>import('./trabaja-nosotros/trabaja-nosotros.module').then(x=> x.TrabajaNosotrosModule)
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
