import { Menu, Scroll } from './../../interface/menu';
import { Component, HostListener, OnInit } from '@angular/core';
import { BreakpointObserver, BreakpointState } from '@angular/cdk/layout';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  public altoLogo = 130;
  public anchoLogo = 150;
  public altoLogoNotScroll = 130;
  public anchoLogoNotScroll = 150;
  private altoLogoScroll = 90;
  private anchoLogoScroll = 100;
  private IsOpen= false;  

  private scrollAnterior :number= 0;  
  public header ;
  private widthMenu = "220px";
  public showMenuSide = false;
  public showMenuVar = true;
  public menu = new Array<Menu>();
  
  public objetoStyle = {
    'background' : 'transparent',
    '-webkit-box-shadow': '0px',
    '-moz-box-shadow': '0px',
    'box-shadow': '0px'


  };
  constructor(
   public breakpoin: BreakpointObserver,
   private ruta: Router

  ) { 
    
    this.OnCloseMenu()
      this.menu = [{
        nombre:"Inicio",
        icono:"",
        path: "inicio"
      },
      {
        nombre:"Servicios",
        icono:"",
        path: "servicios"
      },
      {
        nombre:`Selección 
        RH`,        
        icono:"",
        path: "trabaja"
      },
      {
        nombre:"Contactanos",
        icono:"",
        path: "contactanos"
      }
    ]
  }

  ngOnInit() {
    this.header = document.getElementById("header");
    this.breakpoin.observe(['(min-width:1024px)']).subscribe((retorno:BreakpointState)=>{      
      
      if(retorno.matches){        
        this.showMenuVar = true;
        this.showMenuSide = false;        
        this.ChangeLogo(130, 150,90,110)                            
      }                  
    })

    this.breakpoin.observe(['(max-width:1023px)']).subscribe((retorno:BreakpointState)=>{            
      if(retorno.matches){        
        
        this.showMenuVar = false;
        this.showMenuSide = true;                      
      }
                  
    })

    this.breakpoin.observe(['(min-width:769px)']).subscribe((retorno:BreakpointState)=>{
      
      if(retorno.matches)
      {                
        this.widthMenu="220px"
        this.ChangeLogo(130, 150,90,110)                      
      }
        
      
    })


    this.breakpoin.observe(['(max-width:768px)']).subscribe((retorno:BreakpointState)=>{
      
      if(retorno.matches)
      {                
        this.widthMenu = "170px"
        this.ChangeLogo(90, 120, 70,100)                        
      }              
    })

    
    this.breakpoin.observe(['(max-width:480px)']).subscribe((retorno:BreakpointState)=>{
      
      if(retorno.matches)
      {                
        this.widthMenu = "150px"
                            
      }
        
      
    })
    
    this.breakpoin.observe(['(min-width:481px)']).subscribe((retorno:BreakpointState)=>{
      
      if(retorno.matches)
      {                
        this.widthMenu = "170px"
              
      }              
    })

    
    
  }
  ChangeLogo(altoNotScroll:number, anchoNotScroll:number, altoScroll:number, anchoScroll: number)
  {
    this.altoLogoNotScroll = altoNotScroll;
    this.anchoLogoNotScroll = anchoNotScroll;
    this.altoLogoScroll = altoScroll;
    this.anchoLogoScroll = anchoScroll;
    this.altoLogo = this.altoLogoNotScroll;
    this.anchoLogo = this.anchoLogoNotScroll;

    if(window.scrollY > 70)
    {
      this.altoLogo = this.altoLogoScroll;
      this.anchoLogo = this.anchoLogoScroll;
    }else{
      this.altoLogo = this.altoLogoNotScroll;
      this.anchoLogo = this.anchoLogoNotScroll;
    }
  }
  @HostListener('window:scroll',['$event']) onScrollEvent($event)
  {
    
        
    
    if(window.scrollY > 70)
    {
      this.objetoStyle = {
        'background' : 'white',            
        '-webkit-box-shadow': '4px 9px 6px -6px rgba(0,0,0,0.75)',
        '-moz-box-shadow': '4px 9px 6px -6px rgba(0,0,0,0.75)',
        'box-shadow': '4px 9px 6px -6px rgba(0,0,0,0.75)'

      }

      this.anchoLogo = this.anchoLogoScroll;
      this.altoLogo =this.altoLogoScroll;
    }else{
      this.objetoStyle = {
        'background' : 'transparent',        
        '-webkit-box-shadow': '0px 0px 0px 0px white',
        '-moz-box-shadow': '0px 0px 0px 0px white',
        'box-shadow': '0px 0px 0px 0px white'
      }
      this.anchoLogo =this.anchoLogoNotScroll
      this.altoLogo =this.altoLogoNotScroll;
    }

    
  }

  OpenMenu()
  {
      let menu = document.getElementById("menu");
      menu.style.width = this.widthMenu;
      this.IsOpen = true;
      
      
      
  }

  CloseMenu()
  {
      let menu = document.getElementById("menu");
      menu.style.width = "0px";
      this.IsOpen = false;
      
  }

  OnCloseMenu()
  {
    window.addEventListener("scroll", ()=>{
        this.CloseMenu();
    })

    window.addEventListener("resize", ()=>{
      this.CloseMenu();
  })

  window.addEventListener("click", (event:any)=>{
    
    
    const boton = document.getElementById("boton-menu");
    const menu = document.getElementById("menu");
    if(!boton.contains(event.target) && !menu.contains(event.target) && this.IsOpen)    
    {      
        this.CloseMenu();        
    }

  })
    
  }

  GoTo(modulo : string)
  {
    this.ruta.navigate([modulo])    
    Scroll.move();
    if(this.IsOpen)
    {
      this.CloseMenu();
    }
  }

}

