import { Disparador } from './interface/menu';
import { Platform } from '@angular/cdk/platform';
import { DatePipe } from '@angular/common';
import { Component, EventEmitter, Input, Output ,OnInit, HostListener } from '@angular/core';
import { NavigationStart, Router, Routes } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'IntegrityWebSite';
  @Output("splash") event = new EventEmitter();
  public splashShow =true ;
  constructor(

    date: DatePipe,
    private ruta: Router    
    
  )
  {
    this.OnCloseSplash();
   
    this.ruta.events.subscribe((x)=>{            
      
      if(x instanceof NavigationStart  )
      {        
        this.splashShow =true;          
        x.url = x.url.substr(0, x.url.indexOf(';') === -1 ? x.url.length :x.url.indexOf(';'));
        
        if(this.ruta.config.filter(recorre=> `/${recorre.path}` === x.url).length === 0)
        {
          this.ruta.navigate(["inicio"]);
        }
      }
    })        
  }
  
  
  @HostListener("window:load", ["$event"])  onLoad($event)
  {
    setTimeout(()=>{
      this.splashShow = false;
    }, 1500)
  }

  OnCloseSplash()
  {
    Disparador.OnChangeComponent.subscribe(()=>{
      setTimeout(()=>{
        this.splashShow = false;
      }, 1500)            
    })
  }

  ngAfterContentChecked() {
    
  }
 

  OnChangeComponent()
  {
    Disparador.OnChangeComponent.subscribe(()=>{
      setTimeout(()=>{
        this.splashShow = false;
      }, 2000)
    })
  }
  ngOnInit()
  {
    window.addEventListener("scroll", ()=>{
      if(this.splashShow)
      {
        window.scrollTo(0,0);
      }    
    })
  }

}
